@import './vars';

.category {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    &__wrapper {
        max-width: 100%;
    }
}

.category-banner {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 100px;
    background-color: #fff;
    margin: 20px 0;
    box-shadow: 0 0 2px 1px rgba(0,0,0,0.1);
    img {
        max-height: 90%;
    }
    > * {
        margin: 0 20px;
    }
    &__info {
        h2, p {
            margin: 0;
        }
    }
}

.category-options {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 20px;
    > div {
        margin: 0 20px;
    }
    &__selects {
        display: flex;
    }
    @include screen('mobile') {
        flex-direction: column;
    }
}

.product-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    @include screen('desktop-small') {
        grid-template-columns: repeat(3, 1fr);
    }
    @include screen('tablet') {
        grid-template-columns: repeat(2, 1fr);
    }
    @include screen('mobile') {
        grid-template-columns: repeat(1, 1fr);
    }
}

.product-card {
    width: 240px;
    background-color: #fff;
    padding: 10px;
    user-select: none;
    cursor: pointer;
    box-shadow: 0 0 3px 1px rgba(128, 128, 128, 0.5);
    a {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        height: 100%;
    }
    img {
        max-width: 100%;
        max-height: 260px;
    }
    h3 {
        font-size: 16px;
        color: $accent;
        text-decoration: none;
        min-height: 2.5rem;
        margin: 10px 0;
    }
    p {
        font-size: 14px;
        text-align: left;
        margin: 0;
    }
    &:hover {
        box-shadow: 0 0 4px 2px rgba(128, 128, 128, 0.5);
    }
    &__price {
        display: block;
        font-size: 24px;
        font-weight: 700;
        text-align: left;
        margin-top: 10px;
    }
    &__image {
        display: flex;
        justify-content: center;    
        align-items: center;
        height: 300px;
        width: 100%;
    }
    @include screen('mobile-tablet') {
        width: initial;
    }
}

.pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}

#range-slider {
    width: 300px;
    @include screen('tablet') {
        width: 200px;
    }
}